.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 2rem;
  padding: 20px;
  color: #eee;
  font-size: 10px;
  -ms-flex-preferred-size: 100%;
      flex-basis: 100%;
}

.App-title {
  font-size: 3.3em;
}

.App-content{
  display: -ms-flexbox;
  display: flex;
}

@media(max-width: 500px){
  .App-title {
    font-size: 2.3em;
  }

  .App-content{
    -ms-flex-direction: column;
        flex-direction: column;
  }
}




